<script setup>
import DataService from "@/services/DataService";

import { DynamicForm } from 'tg-controls_cli/dist/myLib.common'
</script>
<template>
    <div>
        <!-- <loader :isVisible="isLoading"></loader> -->
        <section>
            <div class="my-popups">
                <div class="modal d-block ">
                    <div class="modal-dialog modal-dialog-centered ui-draggable">
                        <div class="modal-content">
                            <div class="modal-header ui-draggable-handle">
                                <button type="button" class="close" @click=closepopup($event)>
                                    <em aria-hidden="true" class="fa fa-times"></em>
                                </button>
                                <h4 v-if="this.CustomlayoutId == null" class="modal-title">
                                    {{ $t('AddLayout') }}
                                </h4>
                                <h4 v-else="this.CustomlayoutId !=null" class="modal-title">
                                    {{ $t('CloneLayout') }}
                                </h4>
                            </div>
                            <div class="modal-body">
                                <Form ref="form">
                                    <DynamicForm lang="en" :buttons="buttons" ref="AddLayoutPopup" :schema="FormSchema" 
                                        v-on:OnSubmit="onSubmit" />
                                </Form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="my-popups" v-if="AssignRolePopup">
                <div class="modal d-block ">
                    <div class="modal-dialog modal-dialog-centered ui-draggable">
                        <div class="modal-content">
                            <div class="modal-header ui-draggable-handle  py-0">
                                <h4 class="modal-title py-3 ">
                                    {{ $t('AssignRole') }}
                                </h4>
                            </div>
                            <div class="modal-body">
                                <div class="table-responsive table-fix-header">
                                    <table id="example" class="table table-bordered mt-0" style="width:100%"
                                        aria-label="RolesCheckData listing">
                                        <thead class="thead-bg">
                                            <tr class="text-center">
                                                <th class="text-center">
                                                    <span>Layout Name</span>
                                                </th>
                                                <th class="text-center">
                                                    <span>PlatForm</span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="item in RolesCheckData" :key="item">
                                                <td class="text-center">
                                                    <span class="text-center">
                                                        {{ item.layoutname }}
                                                    </span>
                                                </td>
                                                <td class="text-center">
                                                    <span class="text-center">
                                                        {{ item.platform }}
                                                    </span>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div class="row">
                                    <div class="col-12 mt-2">
                                        <div class="float-right">
                                            <a class="btn btn-success mr-2 my-sm-0 " href="javascript:;"
                                                :title="$t('Submit')" v-on:click="closeAssignpopup($event)">
                                                <em class="fa fa-floppy-o pl-2 pr-2"></em>
                                                {{ $t("un assgin from previous layout and assign to new") }}
                                            </a>
                                            <button class="btn btn-primary delete-btn my-sm-0" :title="$t('Cancel')"
                                                v-on:click="roleListingDelete($event)">
                                                <em class="fa fa-times pl-2 pr-2"></em>
                                                {{ $t("Cancel") }}
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
export default {
    components: { DynamicForm },
    props: {
        showlayoutpopup: {
            type: Boolean
        },
        CustomlayoutId: {
            type: Number,
            required: true
        },
        openclonepopup:{
            type: Boolean
        }
    },
    data() {
        return {
            isLoading: false,
            headertext: '',
            submoduleId: '',
            layoutName: '',
            platForm: '',
            SharetoRole: [],
            layoutType: '',
            description: '',
            moduleNameList: [],
            subModuleNameList: [],
            shearedtypeRole: [],
            searchable: true,
            moduleId: null,
            userTypeId: 1,
            reqFrom: null,
            loggeduserid: '',
            companyId: '',
            ddlModuleId: '',
            pageSize: 10000,
            pageNum: 1,
            role_id: '',
            Options: [],
            Cloneheader: false,
            ShareToItems: [],
            fieldValue: '',
            ModuleName: '',
            SubmoduleName: '',
            RolesCheckReturnData: [],
            RolesCheckData: [],
            ShareToRoleitem: '',
            fetchuserdata:[],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: "Module Name",
                            name: "ModuleName",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetModuleId
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "SelectField",
                            label: "Sub Module Name",
                            name: "SubModuleName",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "TextField",
                            label: "Layout Name",
                            name: "LayoutName",
                            value: "",
                            placeholder: "Enter Layout Name",
                            validationRules: { "required": true },
                            config: {
                                onChange: function (e, field) {
                                    console.log("On Change Textbox", e)
                                },
                                onBlur: function (e, field) {
                                    console.log("On Change Textbox", e)
                                }
                            }
                        },
                        {
                            astype: "SelectField",
                            label: "Platform",
                            name: "Platform",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [{ value: "Web", name: "Web" }, { value: "Mobile", name: "Mobile" }],
                                showAddIcon: false,
                                onChange: function (e, field) {
                                },
                                onAddButtonClick: function (e, field) {
                                }
                            },
                            validationRules: "required",
                        },
                        {
                            astype: "MultiSelectField",
                            label: "Layout Type",
                            //mode: "tag",
                            name: "LayoutType",
                            value: [],
                            placeholder: "Select Layout Type",
                            disabled: false,
                            config: {
                                options: [{ value: "Add or edit", name: "Add or edit" }, { value: "View", name: "View" }, { value: "QuickAdd", name: "Quick Add" }],
                                showAddIcon: false,
                                onChange: this.selectlayouttype
                                
                            },
                            validationRules: "required",
                        },
                         {
                            astype: "TextAreaField",
                            label: "Description",
                            name: "Description",
                            value: "",
                            placeholder: "",
                            validationRules: "required",
                        }
                    ]
                },
                {
          layoutType: "Single",
          Data: [
            {
              astype: "RadioField",
              label: 'Share with',
              name: "Sharewithtype",
              value: "",
              placeholder: "",
              visibility: false,
              config: {
                options: [{
                  value: "User",
                  name: "User"
                },
                {
                  value: "Role",
                  name: "Role"
                }
                ],
                showAddIcon: false,
                onChange: this.SelectshareType
              },
              validationRules: "",
            },
          ]
        },
        {
          layoutType: "double",
          Data: [
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('User '),
                            name: "User",
                            value: "",
                            placeholder: "Please Select User ",
                            visibility: false,
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetUSerId,
                                onRemove: this.onRemoveuser
                            },
                            placeholder: this.$t('SelectUser'),
                            validationRules: { "required": false, max: 100 },
                        },
                        {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: this.$t('Role '),
                            name: "Role",
                            value: "",
                            placeholder: "Please Select Role ",
                            visibility: false,                         
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetRolesID
                            },
                            placeholder: this.$t('SelectUser'),
                            visibility: false,
                            validationRules: { "required": false, max: 100 },
                        },                        
                    ],
        },
        {
            layoutType: "double",
            Data:[
            {
                            astype: "CustomSelectField",
                            mode: "tag",
                            label: "Extra Feature",
                            name: "ExtraFeature",
                            value: "",
                            placeholder: "",
                            disabled: false,
                            visibility: false,
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                          //  validationRules: "",
                        }
            ]
        }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success saveSendforApproval",
                    text: "<i class='fa fa-save pr-2'></i> Save",
                    //onClick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i> Cancel",
                    onClick: this.onCancel
                }
            ],
            AssignRolePopup: false,
            shareroleids:'',
            sharedtoUser:true,
            sharedtoUserIds:[],
            sharedtoRoleIds:[],
            ShareRole:[],
            detailsIDs:[],
            sharetovalue:'',
            FeatureListData:[],
            selectQuickadd:false  ,
            shareto:[] ,
            values:[],
            arrayValues:[]   
        }
    },
    async created() {
        //debugger
        var vm = this;
        // if((vm.FormSchema)[0].Data[0].value==""){
        //     (vm.FormSchema)[3].Data[0].visibility==false;
        // }
       //vm.openclonepopup=false;
        vm.loggeduserid = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.CompanyId;
        vm.FetchModuleData();
        await vm.FetchRoleData();
        vm.FetchClone();
        vm.FetchData();      
       
    },
    methods: {  
       
        // GetFeature (){
        //     var vm =this;
        //      var modulename = vm.ModuleName;
        //      var param = "moduleName=" + modulename + "&type=EXTRA_FEATURES"
        //      DataService.GetDDLData(param).then((response) => { 
        //        if (response.data.DATA != null) {
        //             vm.FeatureListData = response.data.DATA;
        //             vm.FeatureListData.forEach(item=> {
        //                 (vm.FormSchema)[3].Data[0].config.options.push({
        //                     value: item.value,
        //                     name: item.name
        //                 })
        //             })
        //            vm.$refs.AddLayoutPopup.UpdateKeyValue();
        //         }
        //     });
        // },
    onRemoveuser(x, field) {
    var vm = this;
    vm.shareto.forEach((element,index)=> {
        if (element === x.label) {
            vm.shareto.splice(index, 1);
        }
    });
    (vm.FormSchema)[2].Data[0].config.options.forEach((items,index)=>{
        vm.detailsIDs.forEach((data,ind)=>{
            if(items.value === data )
            debugger
            vm.detailsIDs.splice(ind, 1); 
        })
    })
    (vm.FormSchema)[2].Data[1].config.options.forEach((itemx,index)=>{
        vm.detailsIDs.forEach((data,ind)=>{
            if(itemx.value === data )
            vm.detailsIDs.splice(ind, 1); 
        })
    })
},
     showshareuserRole() {
            var vm = this;
              vm.FormSchema[2].Data[0].visibility=false;
               vm.FormSchema[2].Data[1].visibility=false;
               vm.FormSchema[1].Data[0].visibility=false;
               //vm.FormSchema[1].Data[1].visibility=false;
        },
        selectlayouttype(e, field, value){
        var vm = this;
        vm.layoutType=value.value;
        vm.FormSchema[0].Data[4].value.value=vm.layoutType;
            if(value.value == "QuickAdd"){
                vm.selectQuickadd=true;
               vm.FormSchema[2].Data[1].value='';
               vm.FormSchema[2].Data[0].visibility=false;
               vm.FormSchema[2].Data[1].visibility=false;
               vm.FormSchema[1].Data[0].visibility=false;
            }
            else if (value.value == "Add or edit" && vm.openclonepopup==true || value.value == "View" && vm.openclonepopup==true  ) {
                vm.selectQuickadd=false;
            vm.showshareuserRole();
            }
            else{
                vm.FormSchema[2].Data[0].visibility=true;
                vm.FormSchema[2].Data[1].visibility=false;
                vm.FormSchema[1].Data[0].visibility=true;
                //vm.FormSchema[1].Data[1].visibility=true;
            } 
         
        },
    SelectshareType(event, field) {
      var vm = this;
      field.value = event.target.getAttribute('data-value');
      if (field.value == 'User') {
        vm.FormSchema[2].Data[1].value='';
        vm.FormSchema[2].Data[0].visibility = true;
        vm.FormSchema[2].Data[1].visibility = false;
        vm.FetchData()
      } else {
        vm.FormSchema[2].Data[1].value='';
        (vm.FormSchema)[2].Data[0].visibility = false;
        (vm.FormSchema)[2].Data[1].visibility = true;
        vm.FetchRoleData();
      }
    //   vm.BindUserRoleValues();
     vm.$refs.AddLayoutPopup.validateForm.reset();
    },
    GetUSerId(e, field, uservalue){ 
        var vm = this;
        vm.sharedtoUser=true;
        },
        GetRolesID(e, field, Rolevalue){
        var vm = this;
        vm.sharedtoUser=false;
        },
    FetchData: function () {
      var vm = this;
    //   if ((vm.FormSchema)[1].Data[0].value = 'User') {
        vm.isLoading = true;
        DataService.UserDDL().then(response => {
          if (response.data != '' && response.data != null) {
            // let fetchuserdata = [];
            vm.fetchuserdata = response.data;
            vm.FormSchema[2].Data[0].config.options = []
            vm.fetchuserdata.forEach((item) => {
              (vm.FormSchema)[2].Data[0].config.options.push({
                value: item.userid,
                name: item.username
              })
            })
          }
        vm.$refs.AddLayoutPopup.UpdateKeyValue();
          vm.isLoading = false;
        }).catch(err => {
          console.log(err)
        })
      //}
    },
        roleListingDelete: function (e) {
            var vm = this;
            vm.AssignRolePopup = false;
            (vm.FormSchema)[0].Data[5].value.splice(-1, 1);
        },
      
        FetchModuleData: function () {
            var vm = this;
            var params = `moduleId=&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params).then(response => {
                if (response.data.Data != null) {
                    vm.moduleNameList = response.data.Data;
                    vm.moduleNameList.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            value: item.module_id,
                            name: item.module_name
                        })
                    })
                   vm.$refs.AddLayoutPopup.UpdateKeyValue();
                }
            })
            
        },
        GetModuleId(e, field, moduleId) {
            var vm = this;
            if (moduleId) {
                vm.ddlModuleId = moduleId;
            } else {
                vm.ddlModuleId = e.target.value;
            }
            (vm.FormSchema)[0].Data[1].config.options = [];
           
            var params = `moduleId=${vm.ddlModuleId}&userTypeId=1&requestFrom=&isCustomFieldEnable=1`;
            DataService.Modulenamedata(params).then(response => {
                if (response.data.Data != null) {
                    vm.subModuleNameList = response.data.Data;
                    vm.subModuleNameList.forEach((item, index) => {
                        if(item.sub_module_code=="MANAGE_PRODUCT"){
                            item.sub_module_name="Manage Product"
                        }
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            value: item.sub_module_id,
                            name: item.sub_module_name
                        })
                    })
                    vm.$refs.AddLayoutPopup.UpdateKeyValue();
                }
            })
            var id = (vm.FormSchema)[0].Data[0].value;
            (vm.FormSchema)[0].Data[0].config.options.forEach(x=> {
                       if(x.value === id){
                        vm.ModuleName=x.name
                }
            })
        //     if(vm.ModuleName =='Ticketing'){
        //         vm.FormSchema[3].Data[0].visibility=true;
        //         vm.GetFeature();
        //    }
        //   else{
        //     vm.FormSchema[3].Data[0].visibility=false;
        //   }
         
        },
        FetchRoleData: async function (e, field) {
            var vm = this;
            var params = `companyId=${vm.companyId}&pageSize=${vm.pageSize}&statusId=1001`;
            await DataService.Roledata(params).then(function (response) {
                if (response.data.length != null) {
                    vm.shearedtypeRole = response.data;
                    vm.shearedtypeRole.forEach((item, index) => {
                        (vm.FormSchema)[2].Data[1].config.options.push({
                            value: item.rolE_ID,
                            name: item.rolE_NAME
                        });
                    });
                }
                
            });
            vm.$refs.AddLayoutPopup.UpdateKeyValue();
            vm.isLoading = false;
        },
        closepopup(item) {
            var vm = this;
            vm.$emit('closepopup', item);
            vm.$emit('refreshlist', item);
        },
        closeAssignpopup() {
            var vm = this;
            vm.AssignRolePopup = false;
        },
        onCancel: function ($event) {
            var vm = this;
            (vm.FormSchema)[0].Data[1].config.options = [];
            this.closepopup($event);
        },
        FetchClone: function () {
             debugger
            var vm = this;
            vm.Cloneheader = true;
            vm.showshareuserRole();
            // (vm.FormSchema)[3].Data[0].visibility==true;
            var params = `custom_layout_id=${vm.CustomlayoutId}`;
            DataService.GetCustomLayoutPopUp(params).then(function (response) {
                if (response.data) {
                    var CloneDatA = JSON.parse(response.data);
                    var object = [];
                    // var featureid= [];
                    // if(CloneDatA.data[0].extra_feature_id != null){
                    //  featureid.push({
                    //     label: CloneDatA.data[0].TEF[0].feature_name,
                    //     value: CloneDatA.data[0].extra_feature_id
                    //  })
                    // }
                    vm.ShareRole = CloneDatA.shareto;
                    // vm.BindUserRoleValues();
                    vm.GetModuleId('', '', CloneDatA.data[0].MODULE_ID)
                    if(CloneDatA.data[0].layout_type != null){
                        var layoutType = CloneDatA.data[0].layout_type;
                            
                            (vm.FormSchema)[0].Data[4].config.options.forEach(x => {
                                if(layoutType=='Add,Edit'){
                                    layoutType='Add or edit'
                                }
                                if(layoutType == x.value){
                                    object.push({
                                           label: x.name,
                                           value: x.value
                                    })
                                }
                            })
                    }
                    if (CloneDatA != null) {
                        // vm.ShareRole.forEach((item,index) =>{
                        //     var data=[];
                        //     data.push(item.CFM);
                        //     if(data != null ){
                        //         data.forEach(items =>{
                        //            var value='';
                        //            value=items.share_based_on; 
                        //         })
                        //     }
                        // })
                            vm.GetModuleId(null, null, CloneDatA.data[0].MODULE_ID);
                            (vm.FormSchema)[0].Data[0].value = CloneDatA.data[0].MODULE_ID;
                            (vm.FormSchema)[0].Data[0].disabled = true;
                            if(vm.FormSchema[0].Data[0].disabled==true){
                                //vm.FormSchema[3].Data[0].visibility=true;
                                var id = (vm.FormSchema)[0].Data[0].value;
                               (vm.FormSchema)[0].Data[0].config.options.forEach(x=> {
                                    if(x.value === id){
                                    vm.ModuleName=x.name
                                   }
                               })
                               //  vm.GetFeature();
                            }
                            (vm.FormSchema)[0].Data[1].value = CloneDatA.data[0].sub_module_id;
                            (vm.FormSchema)[0].Data[1].disabled = true;
                            //(vm.FormSchema)[0].Data[2].value = CloneDatA.data[0].layout_name;
                            (vm.FormSchema)[0].Data[3].value = CloneDatA.data[0].platform;
                            (vm.FormSchema)[0].Data[5].value = CloneDatA.data[0].description;
                            //(vm.FormSchema)[0].Data[4].value = objData;
                            (vm.FormSchema)[0].Data[4].value = object;
                            // if(featureid.length != 0 ){
                            //     (vm.FormSchema)[3].Data[0].visibility=true;
                            // }
                            // (vm.FormSchema)[3].Data[0].value = featureid;
                           // vm.$refs.AddLayoutPopup.UpdateKeyValue();
                           // vm.sharetovalue = CloneDatA.shareto[0].share_based_on;
                        }
                    }
            })
        }, 
        onSubmit(e, field) {
            var vm = this;
            vm.isLoading = true;
            vm.fieldValue = e;
            let layoutType = [];
            if (vm.fieldValue.LayoutType != null) {
                layoutType.push(vm.fieldValue.LayoutType.value);
            }
            var detailsIDs = [];         
                if(vm.sharedtoUser==true){
                    if(vm.FormSchema[2].Data[0].value){
                        vm.FormSchema[2].Data[0].value.forEach( items => {
                       vm.detailsIDs.push(items.value)
                   }) 
                }                        
            }  
            if(vm.FormSchema[2].Data[1].value){
                    vm.FormSchema[2].Data[1].value.forEach( items => {
                    vm.detailsIDs.push(items.value)
            }) 
        }
            // if(layoutType.includes("Add") && !layoutType.includes("Edit")){
            //     layoutType.push("Edit");
            // }
          
            if (vm.fieldValue != undefined || vm.fieldValue != null) {
                if (vm.CustomlayoutId == null || vm.CustomlayoutId == '') {
                    if(vm.fieldValue.ExtraFeature[0]){
                        var extrafeature = vm.fieldValue.ExtraFeature[0].value;
                    }
                    else{
                        extrafeature='';
                    }
                    var params = `module_id=${vm.fieldValue.ModuleName}&sub_module_id=${vm.fieldValue.SubModuleName}&layout_name=${vm.fieldValue.LayoutName}&platform=${vm.fieldValue.Platform}&layout_type=${layoutType.join(",")}&description=${vm.fieldValue.Description}&share_based_on=${(vm.FormSchema)[1].Data[0].value}&rolesdetail=${vm.detailsIDs}&extra_feature_id=${extrafeature}`;
                    DataService.AddCustomLayoutData(params).then(function (response) {
                        if (response.data.code == "-2") {
                            vm.ShowAlert(vm.$t('AddLayoutError', [vm.$t('Custom_Layout')]), "failure", true, vm.$t("Alert"));
                        }
                        else if (response.data.code == "3") {
                            vm.ShowAlert(vm.$t('AddLayoutSystemFieldError', [vm.$t('Custom_Layout')]), "failure", true, vm.$t("Alert"));
                        }
                        else if (response.data.code == "-5") {
                            vm.ShowAlert( vm.$t('addlayoutforQuickadd'), "warning", true, vm.$t("Alert"));
                        }
                       else if (response.data.code == "-6") {
                        vm.shareto= response.data.otherData;
                        vm.values = response.data.otherData.split(',');
                        vm.arrayValues = [];
                            if(vm.FormSchema[2].Data[0].value){
                                vm.FormSchema[2].Data[0].value.forEach(x=>{
                                    vm.arrayValues.push(x.label);
                                    vm.shareto = vm.values.filter(value => vm.arrayValues.includes(value.trim()));
                                })
                                if(vm.shareto.length>=0){
                                    vm.ShowAlert('This Layout is already share to ' + vm.shareto, "warning", true, vm.$t("Alert"));                                }
                            }
                            else if(vm.FormSchema[2].Data[1].value){
                                vm.FormSchema[2].Data[1].value.forEach(y=>{
                                    vm.arrayValues.push(x.label);
                                    vm.shareto = vm.values.filter(value => vm.arrayValues.includes(value.trim()));
                                })
                                if(vm.shareto.length>=0){
                                 vm.ShowAlert('This Layout is already share to ' + vm.shareto, "warning", true, vm.$t("Alert"));
                                }
                            }
                         }
                        else if (response.data.code >= 1) {
                            vm.ShowAlert(vm.$t("CustomFieldsAdd", [vm.$t("Custom_Layout")]), 'success', true, vm.$t('Alert'));
                            vm.$router.push({
                                name: 'ManageCustomLayout',
                                params: { CustomlayoutId: response.data.code }
                            })
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddLayoutSystemFieldError', [vm.$t('Custom_Layout')]), "failure", true, vm.$t("Alert"));
                        }
                        vm.FormSchema[2].Data[0].value='';
                        vm.FormSchema[2].Data[1].value='';
                        vm.shareto=[];
                        vm.detailsIDs=[];
                        values = [];
                        arrayValues=[];
                    })
                }
                else if (vm.CustomlayoutId != null || vm.CustomlayoutId != '') {
                    vm.isLoading = false;
                    if(vm.fieldValue.ExtraFeature[0]){
                        var extrafeature = vm.fieldValue.ExtraFeature[0].value;
                    }
                    else{
                        extrafeature='';
                    }
                    if (typeof vm.FormSchema[0].Data[4].value.value === 'undefined') {
                        vm.FormSchema[0].Data[4].value.value = vm.fieldValue.LayoutType[0].value;
                    }
                    var params = `custom_layout_id=${vm.CustomlayoutId}&layout_name=${vm.fieldValue.LayoutName}&layout_type=${vm.FormSchema[0].Data[4].value.value}&description=${vm.fieldValue.Description}&platform=${vm.fieldValue.Platform}&share_based_on=&rolesdetail=&extra_feature_id=${extrafeature}`;
                    DataService.SaveCloneCustomLayout(params).then(function (response) {
                        if (response.data == "1") {
                            vm.ShowAlert(vm.$t("CustomFieldsAdd", [vm.$t("Custom_Layout")]), 'success', true, vm.$t('Alert'));
                            vm.closepopup();
                        }
                       else if (response.data == "2") {
                        vm.ShowAlert(vm.$t('AddLayoutError', [vm.$t('Custom_Layout')]), "failure", true, vm.$t("Alert"));
                        }
                        else if (response.statusText == "3") {
                            vm.ShowAlert(vm.$t('Talygen_coustom_Field not exists', [vm.$t('Custom_Layout')]), "failure", true, vm.$t("Alert"));
                        }
                        else{
                            vm.ShowAlert(vm.$t('AddLayoutSystemFieldError', [vm.$t('Custom_Layout')]), "failure", true, vm.$t("Alert"));
                        }
                    })
                }
            }
            vm.isLoading = false;
            vm.FetchRoleData(vm.fieldValue);
        }
    }
}
</script>